import React, { Component } from 'react';
import Movies from '../containers/Movies';
import MoviesSliders from '../containers/MoviesSliders';
import Footer from '../components/Footer';
import {connect} from 'react-redux';
import {
  setData_FromSerch ,
  setResponseGenres ,
  pickGenre,
  loadNextMovies_FromSerch

} from '../actions/moviesActions';
import $ from 'jquery';
import axios from 'axios';

import CircularProgress from 'material-ui/CircularProgress';

import {Tabs, Tab} from 'material-ui/Tabs';
import MoviesIcon from 'material-ui/svg-icons/action/theaters';
import TvIcon from 'material-ui/svg-icons/hardware/tv';



class Discover extends Component {
  componentDidMount() {
    this.pageCounter = 2;
    this.props.getGenres() //getting genres before loead
    $(document).ready(function(){
      console.log("doc READy");
      setTimeout(()=>{ $(".circle_progress_container").fadeOut(200);},500)
      $('#genre_button').on("click",function(){
        setTimeout(()=>{
          $("html, body").animate({ scrollTop: $(".movies_container_for_scroll").offset().top }, 600);
        },1000);
      });
      $('#searchForm1').on("submit",function(){
        setTimeout(()=>{
          $("html, body").animate({ scrollTop: $(".movies_container_for_scroll").offset().top }, 600);
        },1000);
      });
      $('#searchForm2').on("submit",function(){
        setTimeout(()=>{
          $("html, body").animate({ scrollTop: $(".movies_container_for_scroll").offset().top }, 600);
        },1000);
      });
    if(window.innerWidth > 700){
      $('.genre_button').on('touchstart touchend',function(e){
        $(this).toggleClass('genre_button_clicked');
      });
    }
    });
  }
  componentWillUnmount() {
    this.pageCounter = 2;
  }
  loadNextMovies = (page,genre_id) => {
    this.props.loadNextMovies_FromSerch(page,genre_id)
    this.pageCounter++;
  }
  render() {
    return (
      <div>
        <div className="circle_progress_container ">
          <CircularProgress size={40} thickness={5} color="#bf2127"/>
        </div>
        <Tabs
          value={this.props.activeTab}>
            <Tab icon={<MoviesIcon/>} value="movie">
          <MoviesSliders/>
          <div className="App">
            <div className="container_all">
            <div className="title_container">
              <p className="heading_main">Explore <span>by</span> title</p>
            </div>
            <form id="searchForm1" onSubmit={(e)=>{
              e.preventDefault()
              this.props.searchTitle(this.inputTitle)
            }} className="title_search_form">
                <input type="text" onChange={(e)=>this.inputTitle=e.target.value} className="input_title" placeholder="Title"/>
            </form>
            <div className="title_container">
              <p className="heading_main">Find <span>by</span> year</p>
            </div>
            <form id="searchForm2" onSubmit={(e)=>{
              e.preventDefault()
              this.props.searchYear(this.inputYear)
            }} className="title_search_form">
                <input type="text" onChange={(e)=>this.inputYear=e.target.value} className="input_title" placeholder="Year"/>
            </form>

            <div className="title_container">
              <p className="heading_main">Discover <span>by</span> ganre</p></div>
            {
              this.props.genres.map((genre,index) => {
                return <button id="genre_button" className="genre_button" onClick={()=>this.props.filterGenre((genre.id).toString(),genre.id)} key={index}>{genre.name}</button>
              })
            }

            <div className="movies_container_for_scroll">
              <Movies data={this.props.data_FromSerch}/>
            </div>

            {(this.props.data_FromSerch.length > 1)?<button className="genre_button" style={{marginTop:"45px"}} onClick={()=>this.loadNextMovies(this.pageCounter,this.props.currentGenrePicked)}>load more</button>:null}


            <br/>
            <br/>
            <br/>
            <br/>
            <br/>
            <br/>
            </div>
        </div>
        <Footer/>
        </Tab>
        <Tab value="tv" icon={<TvIcon/>}>
          <div>
            <p>
              Tabs are also controllable if you want to programmatically pass them their values.
              This allows for more functionality in Tabs such as not
              having any Tab selected or assigning them different values.
              Tabs are also controllable if you want to programmatically pass them their values.
              This allows for more functionality in Tabs such as not
            </p>
          </div>
        </Tab>
      </Tabs>
    </div>
    );
  }

}
const mapStateToProps = (state) => {
  return {
    data_FromSerch: state.moviesReducer.data_FromSerch,
    genres:state.moviesReducer.genres,
    currentGenrePicked: state.moviesReducer.currentGenrePicked,
    activeTab: state.moviesReducer.activeTab
  }
}
//setResponseGenres
//setData_FromSerch loadNextMovies_FromSerch
const mapDispatchToProps = (dispatch) => {
  return {
    loadNextMovies_FromSerch: (page,genre_id) => {
      axios.get("https://api.themoviedb.org/3/discover/movie?api_key=fa155f635119344d33fcb84fb807649b&language=en-US&sort_by=popularity.desc&with_genres="+genre_id+"&page="+page)
      .then(response => {
        dispatch(loadNextMovies_FromSerch(response.data.results))
      })
      .catch(e => console.log(e))
    },
    filterGenre:(genre_id,currentGenrePicked) => {
      axios.get("https://api.themoviedb.org/3/discover/movie?api_key=fa155f635119344d33fcb84fb807649b&language=en-US&sort_by=popularity.desc&with_genres="+genre_id+"&page=1")
      .then(response => {
        dispatch(pickGenre(response.data.results,currentGenrePicked))
      })
      .catch(e => console.log(e))
    },
    getGenres: () => { // get rid off
      axios.get("https://api.themoviedb.org/3/genre/movie/list?api_key=fa155f635119344d33fcb84fb807649b&language=en-US")
      .then(response => {
        dispatch(setResponseGenres(response.data.genres))
      })
      .catch(e => console.log(e))
    },
    searchTitle: (title) => {
        axios.get("https://api.themoviedb.org/3/search/movie?api_key=fa155f635119344d33fcb84fb807649b&query="+title)
        .then(response => {
          console.log(response.data);
          console.log(response.data.results);
          dispatch(setData_FromSerch(response.data.results))
        })
        .catch(e => console.log(e))
    },
    searchYear: (year) => {
      axios.get("https://api.themoviedb.org/3/discover/movie?api_key=fa155f635119344d33fcb84fb807649b&language=en-US&sort_by=popularity.desc&include_adult=false&include_video=false&primary_release_year="+year+"&page=1")
      .then(response => {

        dispatch(setData_FromSerch(response.data.results))
      })
      .catch(e => console.log(e))
    }
  }
}
//api key 155f635119344d33fcb84fb807649b

export default connect(mapStateToProps,mapDispatchToProps)(Discover);



