const moviesSlidersReducer = (
  state={
    moviesForSliders:[],
  },
  action ) => {

  switch (action.type) {
    case "SET_MOVIES_ON_SLIDERS":
      state={
        ...state,
        moviesForSliders: action.payload,
      };
      break;
    }
    return state;
}
export default moviesSlidersReducer;



