import React, { Component } from 'react';
import './styles/moviesHR.css';
import {connect} from 'react-redux';
import { setSliderMoviesOnState } from '../actions/moviesSlidersActions';
import axios from 'axios';
import $ from 'jquery';
import subsMovie from '../assets/substitute_movie.jpg';

class MovieHR extends Component {
  componentDidMount() {
    this.props.setSliderMoviesOnState(Math.floor(Math.random() * 3) + 1);
    setTimeout(()=>{$(".movieHR_main").addClass("fade_in")},1400);
  }
  render() {
    if(this.props.moviesForSliders.length > 1){
      return (
        <div className="movieHR_main">
          <div className="movie_container_hr">
            <img src={(this.props.moviesForSliders[1].poster_path!==null)?
              "http://image.tmdb.org/t/p/w185/" + this.props.moviesForSliders[1].poster_path : subsMovie} alt="img" className="movie_hr_movie_image hr_image1"/>
            <img src={(this.props.moviesForSliders[7].poster_path!==null)?"http://image.tmdb.org/t/p/w185/" + this.props.moviesForSliders[7].poster_path : subsMovie} alt="img" className="movie_hr_movie_image hr_image2"/>
          </div>
          <div className="movie_container_hr">
            <img src={(this.props.moviesForSliders[2].poster_path!==null)?"http://image.tmdb.org/t/p/w185/" + this.props.moviesForSliders[2].poster_path : subsMovie} alt="img" className="movie_hr_movie_image hr_image3"/>
            <img src={(this.props.moviesForSliders[8].poster_path!==null)?"http://image.tmdb.org/t/p/w185/" + this.props.moviesForSliders[8].poster_path : subsMovie} alt="img" className="movie_hr_movie_image hr_image4"/>
          </div>
          <div className="movie_container_hr">
            <img src={(this.props.moviesForSliders[3].poster_path!==null)?"http://image.tmdb.org/t/p/w185/" + this.props.moviesForSliders[3].poster_path : subsMovie} alt="img" className="movie_hr_movie_image hr_image5"/>
            <img src={(this.props.moviesForSliders[9].poster_path!==null)?"http://image.tmdb.org/t/p/w185/" + this.props.moviesForSliders[9].poster_path : subsMovie} alt="img" className="movie_hr_movie_image hr_image6"/>
          </div>
          <div className="movie_container_hr">
            <img src={(this.props.moviesForSliders[4].poster_path!==null)?"http://image.tmdb.org/t/p/w185/" + this.props.moviesForSliders[4].poster_path : subsMovie} alt="img" className="movie_hr_movie_image hr_image7"/>
            <img src={(this.props.moviesForSliders[10].poster_path!==null)?"http://image.tmdb.org/t/p/w185/" + this.props.moviesForSliders[10].poster_path : subsMovie} alt="img" className="movie_hr_movie_image hr_image8"/>
          </div>
          <div className="movie_container_hr">
            <img src={(this.props.moviesForSliders[5].poster_path!==null)?"http://image.tmdb.org/t/p/w185/" + this.props.moviesForSliders[5].poster_path : subsMovie} alt="img" className="movie_hr_movie_image hr_image9"/>
            <img src={(this.props.moviesForSliders[11].poster_path!==null)?"http://image.tmdb.org/t/p/w185/" + this.props.moviesForSliders[11].poster_path:subsMovie} alt="img" className="movie_hr_movie_image hr_image10"/>
          </div>
          <div className="movie_container_hr">
            <img src={(this.props.moviesForSliders[6].poster_path!==null)?"http://image.tmdb.org/t/p/w185/" + this.props.moviesForSliders[6].poster_path : subsMovie} alt="img" className="movie_hr_movie_image hr_image11"/>
            <img src={(this.props.moviesForSliders[12].poster_path!==null)?"http://image.tmdb.org/t/p/w185/" + this.props.moviesForSliders[12].poster_path : subsMovie} alt="img" className="movie_hr_movie_image hr_image12"/>
          </div>
        </div>
      )
    }else  return null
  }

}


const mapStateToProps = (state) => {
  return {
    moviesForSliders: state.moviesSlidersReducer.moviesForSliders,
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    setSliderMoviesOnState: (page) => {
      axios.get("https://api.themoviedb.org/3/movie/upcoming?api_key=fa155f635119344d33fcb84fb807649b&language=en-US&page="+page)
      .then(response => {
        dispatch(setSliderMoviesOnState(shuffleArray(response.data.results)))
      }).catch(e => console.log(e))
    }
  }
}
const shuffleArray = (a) => {
    for (let i = a.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [a[i], a[j]] = [a[j], a[i]];
    }
    return a;
}



export default connect(mapStateToProps,mapDispatchToProps)(MovieHR);



