import React, { Component } from 'react';
import {connect} from 'react-redux';
import $ from 'jquery';
import axios from 'axios';
import { Link ,withRouter } from 'react-router-dom'
import CircularProgress from 'material-ui/CircularProgress';
import {
  getMovieInfoBasedOnId ,
  clearMovieInfo ,
  getMovieYOUTUBEBasedOnId ,
  getMovieSIMILARBasedOnId,
} from '../actions/moviesActions';
import rateStart from '../assets/rate_star.svg';
import Footer from './Footer';
import pocorns from '../assets/pocorns.png';
import no_poster from '../assets/no_poster.png';
import codadrink from '../assets/codadrink.png';

class Movie extends Component {

  loadAllData = (movieID) => {
    $(document).ready(()=>{
        $(window).scrollTop(0);
      	setTimeout(()=>{ $(".circle_progress_container").fadeOut(200);},500)
    });
    console.log("componentDidMount on movie component");
    //this.movieID = parseInt(window.location.href.split("/")[window.location.href.split("/").length-1]);
    this.props.getMovieInfo(movieID);
    this.props.getMovie_YOUTUBE(movieID);
    this.props.getMovie_SIMILAR(movieID);
  }
  componentDidMount() {
    this.loadAllData(this.props.match.params.movieId);
  }
  componentWillReceiveProps(nextProps){
    if(nextProps.match.params.movieId !== this.props.match.params.movieId) {
      console.log(nextProps);
      this.loadAllData(nextProps.match.params.movieId);
    }
  }
  componentWillUnmount() {
    this.props.clearMovieInfo_MoviePage()
  }
  displayGanres = (ganresArray) => {
    let ganress;
    if(ganresArray.length >= 1){
      ganress =   ganresArray.map((ganre,index)=>{
            return <span key={index}>&nbsp;{ganre.name}&nbsp;</span>
        });
    }
    return ganress;
  }
  beautifyDate = (dataString) => {// 2017-01-23
    let month = '';
    switch(dataString.substring(5,7)){
      case "01": month = "Jan"; break;
      case "02": month = "Feb"; break;
      case "03": month = "Mar"; break;
      case "04": month = "Apr"; break;
      case "05": month = "May"; break;
      case "06": month = "Jun"; break;
      case "07": month = "Jul"; break;
      case "08": month = "Aug"; break;
      case "09": month = "Sep"; break;
      case "10": month = "Oct"; break;
      case "11": month = "Nov"; break;
      case "12": month = "Dec"; break;
    }
    return  dataString.substring(8,11) + " " + month + " " + dataString.substring(0,4);
  }
  displayYoutube =  (array) => {
    if(array.length>=1){
      return (
        <div>
          <p className="similar_movieS_title">Trailer</p>
        <div className="youtube_container">
      <iframe width={window.innerWidth} height={(window.innerWidth/1.75)} src={"https://www.youtube.com/embed/"+array[0].key} frameBorder="0" gesture="media" allow="encrypted-media" allowFullScreen></iframe>
    </div></div>
    )
    } else return null;
  }
  displatMoviePoster = (path) => {
    if(path !== undefined && path !== null){
      return (
        <div className="movie_image_div_container">
          <img alt="" src={"http://image.tmdb.org/t/p/w185/"+path} className="movie_image"/>
        </div>
      )
    }else return (
      <div className="movie_image_div_container">
        <img alt="" src={no_poster} className="movie_image"/>
      </div>
    );
  }
  displayTryToWatchOnlineBtn = (name,date) => {
    if (name !== undefined && date !== undefined) {
    //  let movieNameString = name.replace(/[^a-zA-Z ]/g, "").toLowerCase().split(" ").join("-");
      //let movieYear = date.substring(0,4)
      return <a href={"http://0123movies.com/movies-"+name.replace(/[^a-zA-Z ]/g, "").toLowerCase().split(" ").join("-")+"-"+date.substring(0,4)+"-0123movies.html"}
      className="IMDB_link">
      <button className="genre_button">Try to watch online</button>
    </a>
    }
  }
  render() {
    let movie = this.props.thisMovieIdData;
    //const { match } = this.props;
    //console.log(match);
    return (
        <div>
          {(movie!=={})?
            <div className="all_data">

              <div className="circle_progress_container ">
                <CircularProgress size={40} thickness={5} color="#bf2127"/>
              </div>

              <div className="movie_image_div_container_wraper">
                <img src={pocorns} alt="" className="pocorns_img"/>
                <img src={codadrink} alt="" className="codadrink_img"/>
                {
                  this.displatMoviePoster(movie.poster_path)
                }
              </div>

                <p className="movie_title">{movie.title}</p>
                {(movie.tagline)?<p className="movie_tagline">&quot;&nbsp;{movie.tagline}&nbsp;&quot;</p>:<br/>}
                <div className="info_container_table">
                 <p className="movie_rates"> <img src={rateStart} alt="Rate:" />
                  &nbsp;
                  {movie.vote_average} / 10
                  &nbsp; | &nbsp;
                  {movie.vote_count}</p>
                <p  className="table_data_p">Genres:{ this.displayGanres(movie.genres) }</p>

                  <p  className="table_data_p">Release Date: {(movie.release_date)?<span>{this.beautifyDate(movie.release_date)}</span>:null}</p>
                  <p className="table_data_p">Time: <span>{(movie.runtime !== 0)?movie.runtime + "   min":"no info"}</span> </p>
                  <p className="overview_p">{movie.overview}</p>
                </div>
                <div className='IMDB_link_button_container' >
                  <a href={"http://www.imdb.com/title/"+movie.imdb_id} className="IMDB_link">
                    <button className="genre_button">Visit on IMDB</button>
                  </a>
                {this.displayTryToWatchOnlineBtn(movie.title, movie.release_date)}
                </div>

                  {
                    this.displayYoutube(this.props.thisMovieIdDataYOUTUBE.results)
                  }
                {(this.props.thisMovieIdDataSIMILAR.length > 5)?<div>
                  <p className="similar_movieS_title">Similar Movies</p>
                  <div className="youtube_container">
                  <div className="similar_movieS_container">
                   {
                    this.props.thisMovieIdDataSIMILAR.slice(0,4).map((movie,index)=>{
                      return(
                        <Link to={`/movie/${movie.id}`} key={index}>
                          <div  className="similar_movie_container">
                            <div className="similar_movie_img_holder">
                              <img src={"http://image.tmdb.org/t/p/w185/"+movie.poster_path} className="similar_movie_img" alt=""/>
                            </div>
                          </div>
                        </Link>
                        )
                    })
                  }
                </div>
                </div></div>:null}
                <Footer/>
              </div>
          :<p>Loading ...</p>}
        </div>
    )
  }
}
const mapStateToProps = (state) => {
  return {
    thisMovieIdData: state.moviesReducer.thisMovieIdData,
    thisMovieIdDataYOUTUBE: state.moviesReducer.thisMovieIdDataYOUTUBE,
    thisMovieIdDataSIMILAR: state.moviesReducer.thisMovieIdDataSIMILAR,
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    getMovieInfo: (id) => {
      axios.get("https://api.themoviedb.org/3/movie/"+id+"?api_key=fa155f635119344d33fcb84fb807649b&language=en-US")
      .then(response => { dispatch(getMovieInfoBasedOnId(response.data)) })
      .catch(e => console.log(e))
    },
    getMovie_YOUTUBE: (id) => {
      axios.get("https://api.themoviedb.org/3/movie/"+id+"/videos?api_key=fa155f635119344d33fcb84fb807649b&language=en-US&page=1")
      .then(response => { dispatch( getMovieYOUTUBEBasedOnId(response.data)) })
      .catch(e => console.log(e))
    },
    getMovie_SIMILAR: (id) => {
      axios.get("https://api.themoviedb.org/3/movie/"+id+"/similar?api_key=fa155f635119344d33fcb84fb807649b&language=en-US&page=1")
      .then(response => { dispatch( getMovieSIMILARBasedOnId(response.data.results)) })
      .catch(e => console.log(e))
    },
    clearMovieInfo_MoviePage: () => {
      dispatch(clearMovieInfo())
    }
  }
}


export default withRouter(connect(mapStateToProps,mapDispatchToProps)(Movie));



