import React, { Component } from 'react';
import './styles/movies.css';
import { Link } from 'react-router-dom';
import rateStart from '../assets/rate_star.svg';
import no_poster from '../assets/no_poster.png';


class Movies extends Component {

  displatMoviePoster = (path) => {
    if(path !== undefined && path !== null){
      return  <img alt="" src={"http://image.tmdb.org/t/p/w185/"+path} className="movie_holder_img"/>
    }else return <img alt="" src={no_poster} className="movie_holder_img"/>
  }
  renderMovies = () => {
    if(this.props.data.length > 1){
      console.log("Movies Component data props: ", this.props.data);
      let movies = this.props.data.map((movie,index) => {
        return(
          <Link to={"/movie/"+movie.id} className='movie_holder_link' key={index}>
            <div className="movie_holder">
              {this.displatMoviePoster(movie.poster_path)}
              <p className="movie_holder_title">{movie.title}</p>
              <p className="movie_holder_rates">
                <img src={rateStart} alt="Rate:" />
                &nbsp;
                {movie.vote_average}
                &nbsp; | &nbsp;
                {movie.vote_count}</p>
                {(this.props.releaseDate)?
                  <p className="release_date_p">{movie.release_date}</p>
                  :null}
            </div>
          </Link>
        )
      })
      return movies;
    }
  }
  render() {
    return (
      <div className="movies_container">
        {(this.props.data.length > 1)?<div className="separator_wraper"><div className="separator"></div></div>:null}
        {this.renderMovies()}
      </div>
    );
  }

}

export default Movies;



